  from {
    opacity: 0;
    transform: translateY(0) scale(1);
  }
  25% {
    opacity: 1;
  }
  to {
    opacity: 0;
    transform: translateY(-10em) scale(0);
  }
}
.fire-container1 .particle {
  background-image: radial-gradient(#a0a0a0 20%, rgba(255, 80, 0, 0) 70%);
}

.fire-container2 .particle {
  background-image: radial-gradient(#5d5d5d 20%, rgba(160, 160, 160, 0) 70%);
}


